@import "../../assets/scss/app";

.home {
  padding-top: 45px;

  @include desktop {
    padding-top: 65px;
  }

  img {
    width: 100px;
    height: 100px;
  }

  .links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 16px;

    @include desktop {
      padding: 24px;
    }

    .link {
      display: flex;
      flex-direction: column;
      text-decoration: none;
      margin: 8px;

      &:hover p {
        text-decoration: underline;
      }

      img {
        width: 250px;
        height: auto;
        border-radius: 10px;
      }

     p {
       margin-top: 4px;
       color: #000;
       text-align: center;


     }


    }

  }
}